<template>
    <div class="root">
        <div class="video" v-show="isPlay">
            <video class="video-js" id="playerId">
                <source :src="playing_video.CAMERA_VIDEO" type="video/mp4" />
            </video>
            <div class="state_1">
                <span class="temperature">视频当前&nbsp;&nbsp;温度：{{ playing_video.TEMPERATURE
                    }}°C&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span class="wet">湿度：{{ playing_video.HUMIDITY }}%&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <span class="ph">PH: {{ playing_video.PH }}PH&nbsp;&nbsp;&nbsp;&nbsp;</span>
            </div>
        </div>
        <div class="video-list">
            <div v-for="video in videoList" :key="video.id" class="video-item" @click="play(video)">
                <img :src="video.CAMERA_PIC" alt="">
                <div class="state_2">
                    <span class="temperature">温度：{{ video.TEMPERATURE
                        }}°C&nbsp;&nbsp;</span>
                    <span class="wet">湿度：{{ video.HUMIDITY }}%&nbsp;&nbsp;</span>
                    <span class="ph">PH: {{ video.PH }}PH</span>
                </div>
                <div class="icon">
                    <i class="el-icon-video-play"></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            isPlay: false,
            player: null,
            videoList: [],
            playing_video: {}
        };
    },
    mounted() {
        this.getVideo();
    },
    created() {
        this.timer = window.setInterval(() => {
            this.getVideo()
        }, 60000)
    },
    beforeDestroy() {
        clearInterval(this.timer)
        if (this.player) {
            this.player.dispose()
        }
    },
    methods: {
        init_videoJs(poster) {
            let that = this
            that.player = this.videojs('playerId', {
                playbackRates: [0.5, 1, 1.5, 2],
                language: 'zh-CN',
                controls: true,
                autoplay: true,
                preload: false,
                fluid: true,
                loop: false,
                muted: false,
                poster: poster, //播放前显示的视频画面，播放开始之后自动移除。通常传入一个URL
                suppressNotSupportedError: false,
                controlBar: {
                    currentTimeDisplay: true,
                    timeDivider: true,
                    durationDisplay: true,
                    remainingTimeDisplay: false,
                    playbackRateMenuButton: true,
                    volumePanel: {
                        inline: false
                    },
                    children: [
                        { name: 'playToggle' },

                        { name: 'currentTimeDisplay' },
                        { name: 'progressControl' }, // 播放进度条
                        // { // 剩余时间  默认情况下，剩余时间显示为负时间。不显示负号集
                        //     name: 'remainingTimeDisplay',
                        //     displayNegative: false
                        // },
                        { name: 'durationDisplay' },
                        {
                            name: 'playbackRateMenuButton',
                            'playbackRates': [0.5, 1, 1.5, 2, 2.5]
                        },
                        { name: 'volumePanel', inline: false },
                        { name: 'FullscreenToggle' }
                    ]
                }
            }, function onPlayerReady() {
                this.on("play", () => {
                    // alert("视频已经开始播放，祝你有好的体验");
                });
                this.on("pause", () => {
                    // alert("视频已经暂停");
                });
                this.on("ended", () => {
                    // alert("视频播放完成");
                });
                console.log('onPlayerReady', this)
            });
        },
        getVideo() {
            axios.get('http://127.0.0.1:3000/api/video')
                .then((response) => {
                    this.videoList = response.data.data;
                })
        },
        play(video) {
            this.playing_video = video;
            if (!this.isPlay) {
                this.isPlay = true;
            }
            // this.backTop();
            this.init_videoJs(video.CAMERA_PIC);
        },
        // backTop() {
        //     const that = this;
        //     let timer = setInterval(() => {
        //         let ispeed = Math.floor(-that.scrollTop / 5);
        //         document.documentElement.scrollTop = document.body.scrollTop =
        //             that.scrollTop + ispeed;
        //         if (that.scrollTop === 0) {
        //             clearInterval(timer);
        //         }
        //     }, 16);
        // },
    }
};
</script>

<style scoped>
* {
    padding: 0;
    border: 0;
    margin: 0;
}

.root::-webkit-scrollbar {
    display: none;
}

.root {
    height: 850px;
    background-image: url('../assets/dadoumiao.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% auto;
    overflow: scroll;
}

.video {
    width: 900px;
    height: 640px;
    margin-left: 50%;
    transform: translateX(-50%);
    margin-top: 30px;
}

.state_1,
.state_2 {
    display: flex;
    flex-direction: row;
}

.state_1 {
    margin-top: 20px;
    font-size: 16px;
    font-weight: 600;
}

.state_2 {
    margin-top: 5px;
}

.video-list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 40px;
    padding: 0 200px;
}

.video-item {
    position: relative;
    width: 266px;
    height: 245px;
    margin: 0 10px;
}

.video-item img {
    width: 266px;
    height: 150px;
}

.icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 266px;
    height: 150px;
    font-size: 120px;
    text-align: center;
    line-height: 150px !important;
    color: transparent;
    opacity: .8;
}

.icon:hover {
    color: gray;
    display: inline-block;
}
</style>