<template>
    <div class="root">
        <div class="line_1">
            <div id="temperature" class="temperature" style="width: 600px; height: 400px"></div>
            <div id="co2" class="co2" style="width: 600px; height: 400px"></div>
            <div id="light" class="light" style="width: 600px; height: 400px"></div>
        </div>
        <div class="line_2">
            <div class="state">当前水泵状态：{{ beng ? '打开' : '关闭' }}</div>
            <div class="line_3">
                <div id="wet" class="wet" style="width: 1000px; height: 400px"></div>
                <div id="ph" class="ph" style="width: 1000px; height: 400px"></div>

            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data() {
        return {
            timer: "",
            temperatureData: [],
            phData: [],
            co2Data: [],
            lightData: [],
            wetData: [],
            date: [],
            beng: false
        };
    },
    mounted() {
        this.getCollecter();
        this.getController();
    },
    created() {
        this.timer = window.setInterval(() => {
            this.getCollecter();
            this.getController();
        }, 10000)
    },
    beforeDestroy() {
        clearInterval(this.timer)
    },
    methods: {
        getController() {
            axios.get('http://127.0.0.1:3000/api/controller')
                .then((response) => {
                    this.lightData = Array.from(response.data.data, (element) => {
                        return element.LIGHT_RATIO
                    })
                    this.co2Data = Array.from(response.data.data, (element) => {
                        return element.CO2
                    })
                    this.beng = response.data.data[response.data.data.length - 1].PUMP_STATUS
                }).then(() => {
                    this.lightEcharts();
                    this.co2Echarts();
                })
        },
        getCollecter() {
            axios.get('http://127.0.0.1:3000/api/collecter')
                .then((response) => {
                    this.temperatureData = Array.from(response.data.data, (element) => {
                        return element.TEMPERATURE
                    })
                    this.phData = Array.from(response.data.data, (element) => {
                        return element.PH
                    })
                    this.wetData = Array.from(response.data.data, (element) => {
                        return element.HUMIDITY
                    })
                    this.date = Array.from(response.data.data, (element) => {
                        return element.TIME
                    })
                }).then(() => {
                    this.temperatureEcharts();
                    this.phEcharts();
                    this.wetEcharts();
                })
        },
        temperatureEcharts() {
            var chartDom = document.getElementById('temperature');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: '温度'
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {},
                textStyle: {
                    color: 'black',
                    fontWeight: 700
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        dataView: { readOnly: false },
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: this.date
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value} °C'
                    }
                },
                series: [
                    {
                        name: '温度',
                        type: 'line',
                        data: this.temperatureData,
                        markPoint: {
                            data: [
                                { type: 'max', name: 'Max' },
                                { type: 'min', name: 'Min' }
                            ]
                        },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }]
                        }
                    }
                ],
                color: ['#E6A23C']
            };

            option && myChart.setOption(option);
        },
        co2Echarts() {
            var chartDom = document.getElementById('co2');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: ''
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {},
                textStyle: {
                    color: 'black',
                    fontWeight: 700
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        dataView: { readOnly: false },
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: this.date
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value} ppm'
                    }
                },
                series: [
                    {
                        name: '二氧化碳',
                        type: 'line',
                        data: this.co2Data,
                        markPoint: {
                            data: [
                                { type: 'max', name: 'Max' },
                                { type: 'min', name: 'Min' }
                            ]
                        },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }]
                        }
                    }
                ],
                color: ['#909399']
            };

            option && myChart.setOption(option);
        },
        lightEcharts() {
            var chartDom = document.getElementById('light');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: ''
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {},
                textStyle: {
                    color: 'black',
                    fontWeight: 700
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        dataView: { readOnly: false },
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: this.date
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value} '
                    }
                },
                series: [
                    {
                        name: '光照强度',
                        type: 'line',
                        data: this.lightData,
                        markPoint: {
                            data: [
                                { type: 'max', name: 'Max' },
                                { type: 'min', name: 'Min' }
                            ]
                        },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }]
                        }
                    }
                ],
                color: ['#F56C6C']
            };

            option && myChart.setOption(option);
        },
        phEcharts() {
            var chartDom = document.getElementById('ph');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: 'PH'
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {},
                textStyle: {
                    color: 'black',
                    fontWeight: 700
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        dataView: { readOnly: false },
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: this.date
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value} PH'
                    }
                },
                series: [
                    {
                        name: 'PH',
                        type: 'line',
                        data: this.phData,
                        markPoint: {
                            data: [
                                { type: 'max', name: 'Max' },
                                { type: 'min', name: 'Min' }
                            ]
                        },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }]
                        }
                    }
                ],
                color: ['#67C23A']
            };

            option && myChart.setOption(option);
        },
        wetEcharts() {
            var chartDom = document.getElementById('wet');
            var myChart = this.$echarts.init(chartDom);
            var option;

            option = {
                title: {
                    text: ''
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {},
                textStyle: {
                    color: 'black',
                    fontWeight: 700
                },
                toolbox: {
                    show: true,
                    feature: {
                        dataZoom: {
                            yAxisIndex: 'none'
                        },
                        dataView: { readOnly: false },
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: this.date
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value} %'
                    }
                },
                series: [
                    {
                        name: '湿度',
                        type: 'line',
                        data: this.wetData,
                        markPoint: {
                            data: [
                                { type: 'max', name: 'Max' },
                                { type: 'min', name: 'Min' }
                            ]
                        },
                        markLine: {
                            data: [{ type: 'average', name: 'Avg' }]
                        }
                    }
                ],
                color: ['#409EFF']
            };

            option && myChart.setOption(option);
        }
    }
};
</script>

<style>
* {
    padding: 0;
    border: 0;
    margin: 0;
}

.root {
    background-image: url('../assets/dadou.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}

.line_1 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 30px;
    background-color: rgba(255, 255, 255, .8);
}

.line_2 {
    display: flex;
    flex-direction: row;
    padding: 0 200px;
    background-color: rgba(255, 255, 255, .8);
}

.state {
    flex-grow: 3;
    font-weight: 700;
    font-size: 18px;
}

.line_3 {
    flex-grow: 5;
    display: flex;
    flex-direction: column;
}
</style>