import VueRouter from "vue-router";
import DataForm from "../pages/DataForm.vue";
import TableEcharts from "../pages/TableEcharts.vue";
import VideoPlayback from "../pages/VideoPlayback.vue";

const router = new VueRouter({
    routes: [
        {
            name: "DataForm",
            path: "/DataForm",
            component: DataForm,

        }, {
            name: "TableEcharts",
            path: "/TableEcharts",
            component: TableEcharts,
        }, {
            name: "VideoPlayback",
            path: "/VideoPlayback",
            component: VideoPlayback

        }, {
            path: "/",
            redirect: "/TableEcharts"
        }
    ]
})

export default router;
